'use strict';

var titleCase = require('to-title-case');
var React = require('react');
var Reflux = require('reflux');
var Link = require('react-router').Link;
var DGSet = require('../util').DGSet;
var unique = require('../util').unique;
var CountryActions = require('../actions/countryActions');
var MapViewActions = require('../actions/mapViewActions');
var MetaActions = require('../actions/pageMetaActions');
var CountryStore = require('../stores/countryStore');
var FilteredLocationsStore = require('../stores/filteredLocationsStore');
var ProjectsStore = require('../stores/projectsStore');
var TopoLayer = require('./map/topoLayer.jsx');
var ClusterLayer = require('./map/clusterLayer.jsx');
var Popup = require('./map/popup.jsx');
var Filters = require('./filters.jsx');
var Header = require('./header.jsx');
var NotYet = require('./notYet.jsx');
var Footer = require('./footer.jsx');


var Country = React.createClass({displayName: "Country",

  mixins: [
    Reflux.connect(CountryStore, 'countries'),
    Reflux.connect(FilteredLocationsStore, 'locations'),
  ],

  getInitialState: function() {
    return {filtersWidth: 225};
  },

  componentWillMount: function() {
    // make sure we load the country data. http cache should make this cheap.
    CountryActions.loadCountry.triggerPromise(this.props.params.countryId)
      .then(this.countryDidLoad);
  },

  componentWillReceiveProps: function(nextProps) {
    if (nextProps.params.countryId !== this.props.params.countryId) {
      // user navigated from one country directly to another's url
      delete this.country;
      CountryActions.loadCountry.triggerPromise(nextProps.params.countryId)
        .then(this.countryDidLoad);
    }
  },

  countryDidLoad: function(raw) {
    // for now, work with the raw country data.
    // we can't to countryStore.getCountry, because this callback runs before
    // countryStore.onLoadCountryCompleted.
    // we should maybe have another store for the current country...
    this.country = CountryStore.utils.countryToFeature(raw);
    MapViewActions.changeBounds(this.country.properties.bounds, {
      paddingTopLeft: [this.state.filtersWidth, 51],  // filters width and header height
    });
  },

  locationPin: function(featureData, latlng, layerComponent) {
    var size = 16;
    return L.marker(latlng, {
      icon: L.divIcon({
        className: 'project-location-marker',
        iconSize: [size, size],
        iconAnchor: [size/2, size/2],
      }),
    });
  },

  clusterIcon: function(cluster) {
    var childCount = cluster.getChildCount();
    return L.divIcon({
      html: '<span class="marker-number">' + childCount + '</span>',
      className: 'marker-cluster',
      iconSize: L.point(30, 30),
    });
  },

  highlightProject: function(id) {
    var cluster = this.refs.clusterLayer.layer;
    cluster.getLayers()
      .filter(function(layer) {
        return (layer.feature.properties.id === id);
      })
      .map(function(layer) {
        return cluster.getVisibleParent(layer);
      })
      .forEach(this.refs.clusterLayer._addPopupOpenClass);
  },

  clearHighlight: function() {
    var cluster = this.refs.clusterLayer.layer;
    cluster.getLayers()
      .map(function(layer) {
        return cluster.getVisibleParent(layer);
      })
      .forEach(this.refs.clusterLayer._rmPopupOpenClass);
  },

  locationPopup: function(location) {
    var project = ProjectsStore.getById(location.properties.id);
    this.highlightProject(project.id);
    return (
      React.createElement(Popup, null, 
        React.createElement(Popup.Header, null, 
          React.createElement("h3", {className: "panel-title"}, location.properties.locationName)
        ), 
        React.createElement("div", {className: "panel-body location-summary"}, 
          React.createElement("p", null, 
            React.createElement("strong", null, titleCase(location.properties.title), ": "), " Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua."
          ), 
          React.createElement("p", {className: "details"}, 
            React.createElement("span", null, /* need this span wrapper for flexbox! */
              React.createElement("span", {className: "locations-count"}, project.locations.length - 1), " other locations.", ' '
            ), 
            React.createElement(Link, {to: "project", params: {projectId: location.properties.id, countryId: this.country.id}}, 
              "See project details"
            )
          )
        )
      )
    );
  },

  clusterPopup: function(clusterData) {
    var locations = clusterData.features
      .map(function(feature) { return feature.properties; });

    var projects = unique(locations.map(function(l) { return l.id; }))
      .map(ProjectsStore.getById);

    return (
      React.createElement(Popup, null, 
        React.createElement(Popup.Header, null, 
          React.createElement("h3", {className: "panel-title"}, 
            projects.length, " projects with ", locations.length, " locations here"
          )
        ), 
        React.createElement("div", {className: "panel-body"}, 
          React.createElement("ul", {className: "project-list"}, 
            projects.map(function(project) {
              return (
                React.createElement("li", {key: project.id}, 
                  React.createElement(Link, {
                    to: "project", 
                    params: {projectId: project.id, countryId: this.country.id}, 
                    onMouseOver: this.highlightProject.bind(this, project.id), 
                    onMouseOut: this.clearHighlight}, 
                    titleCase(project.title), " (", project.locations.length, " locations)"
                  )
                )
              );
            }, this)
          ), 
          React.createElement("a", {className: "btn btn-link", onClick: this.zoomToCluster(clusterData)}, 
            React.createElement("span", {className: "cluster-count"}, React.createElement("span", {className: "glyphicon glyphicon-zoom-in"})), " Zoom to cluster"
          )
        )
      )
    );
  },

  locationDblclick: function(feature, layer) {  // on a non-cluster location
    MetaActions.transitionTo('project', {
      countryId: this.country.id,
      projectId: feature.properties.id,
    });
  },

  clusterDblclick: function(what) {
    what.layer.zoomToBounds();
  },

  zoomToCluster: function(clusterData) {
    // the outer function is just to close over `clusterData`
    return function() { clusterData.event.layer.zoomToBounds(); };
  },

  filtersWidthChange: function(newWidth) {
    this.setState({filtersWidth: newWidth});
  },

  getPopupPadding: function() {
    return {
      topLeft: [this.state.filtersWidth + 20, 0],
    };
  },

  render: function() {
    var hasLoaded = Boolean(this.country);  // false if this.country is undefined

    var numProjectLocs = hasLoaded ? this.country.properties.locations.length : 0;

    var header =(
      React.createElement(Header, null, 
        React.createElement(Header.Main, null, 
          React.createElement(Header.Item, null, 
            React.createElement(Link, {to: "international", className: "back"}, 
              "Global view"
            )
          ), 
          React.createElement(Header.Item, null, 
            React.createElement("a", {className: "country"}, 
              hasLoaded ? this.country.properties.name : '(loading country...)', 
              hasLoaded && ('  ' + numProjectLocs + ' Project Locations')
            )
          )
        ), 
        React.createElement(Header.Aux, null, 
          React.createElement(Header.Item, {active: true}, 
            React.createElement(NotYet, null, "Thematic Layer")
          ), 
          React.createElement(Header.Item, null, 
            React.createElement(NotYet, null, "Thematic Layer")
          ), 
          React.createElement(Header.Item, null, 
            React.createElement(NotYet, null, "Thematic Layer")
          )
        )
      )
    );

    if (!hasLoaded) {
      return header;
    }

    return (
      React.createElement("div", null, 
        header, 
        React.createElement(ClusterLayer, {
          ref: "clusterLayer", 
          getMap: this.props.getMap, 
          layerData: this.state.locations, 
          showCoverageOnHover: false, 
          zoomToBoundsOnClick: false, 
          spiderifyOnMaxZoom: false, 
          maxClusterRadius: 21, 
          iconCreateFunction: this.clusterIcon, 
          pin: this.locationPin, 
          dblclick: this.locationDblclick, 
          clusterDblclick: this.clusterDblclick, 
          popup: this.locationPopup, 
          popupClose: this.clearHighlight, 
          clusterPopupFunction: this.clusterPopup, 
          getPopupPadding: this.getPopupPadding}), 
        React.createElement(Filters, {notifyWidthChange: this.filtersWidthChange}), 
        React.createElement(Footer, null)
      )
    );
  }

});


module.exports = Country;
